@import '/src/index.css';


.gpt3__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 4rem;
}
.gpt3__navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.gpt3__navbar-links_logo{
    margin-right: 2rem;
    
}
.gpt3__navbar-links_logo img{
    margin-right: 2rem;
    width: 150px;
    height: 62px;

}
.gpt3__navbar-links-container{
    display: flex;
    flex-direction: row;
    
}
.gpt3__navbar-sign {
    display: flex;
    justify-content:flex-end ;
    align-items: center;
}
.gpt3__navbar-links-container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-decoration: capitalize;
    margin: 0 0.9rem;
    cursor: pointer;

}
.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
    width: 152px;
    padding: 0.5rem 1rem;
    background: #ff4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    color: #fff;
    border: 0;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
}
.gpt3__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}
.gpt3__navbar-menu svg {
    cursor: pointer;
}
.gpt3__navbar-menu_container {
    display: flex;
    justify-items: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius:5px ;
    box-shadow: 0 0 5 rgb(0, 0, 0,0.2);
}
.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}
.gpt3__navbar-menu_container-sign {
    display: none;
}
@media screen and (max-width:1050px ) {   /* big screen */
    
    .gpt3__navbar-links-container {
        display: none;
    }
    .gpt3__navbar-menu {
        display: flex;
    }
}
@media screen and (max-width:700px ) {  /*middle screen */
    
    .gpt3__navbar {
        padding: 2rem 4rem;
    }
   
}
@media screen and (max-width:550px ) {  /* mobile version */
    
    .gpt3__navbar {
        padding: 2rem ;
    }
    .gpt3__navbar-sign {
        display: none;
    }
    .gpt3__navbar-menu_container {
        top: 20px;

    }
    .gpt3__navbar-menu_container-links-sign {
        display: block;
    }
   
}