.gpt3__header {
    display: flex;
    
}
.gpt3__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
    
}
.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 45px;
    line-height: 75px;
    letter-spacing: -0.04rem;
}
.gpt3__header-content p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color: #81AFDD;
    margin-top: 1.5rem;
}
.gpt3__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}
.gpt3__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    background:var(--color-footer);

    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    color: #3D5184;
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border: none;
}
.gpt3__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height:50px ;
    background: #FF4920;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    color: #fff;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;

}
.gpt3__header-content__people{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.gpt3__header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}
.gpt3__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gpt3__header-image img {
    width: 300px;
    height: 300px;
}

@media screen and (max-width:1050px) {
    .gpt3__header {
        flex-direction: column;
    }
    .gpt3__header-contetn {
        margin: 0 0 3rem;
    }
  
}
@media screen and (max-width:650px) {
    .gpt3__header h1 {
        font-size: 48px;
        line-height: 60px;
    }
    .gpt3__header  p {
        font-size: 16px;
        line-height: 24px;
    }
    .gpt3__header-content__people {
        flex-direction: column;
    }
    .gpt3__header-content__people p {
        margin: 0;
    }
    .gpt3__header-content__input,
    .gpt3__header-content__input button {
       font-size: 16px;
       line-height: 24px;
    }


}
@media screen and (max-width:490px) {
    .gpt3__header h1 {
        font-size: 36px;
        line-height: 48px;
    }
    .gpt3__header  p {
        font-size: 14px;
        line-height: 24px;
    }

    .gpt3__header-content__people p {
        margin: 0;
    }
    .gpt3__header-content__input,
    .gpt3__header-content__input button {
       font-size: 12px;
       line-height: 16px;
    }

    .gpt3__header-image {
    margin-top: 0.5rem;
}
}